@font-face {
	font-family: "Montserrat";
	src: url("../assets/fonts/Montserrat/Montserrat-Bold.woff") format("woff"),
		url("../assets/fonts/Montserrat/Montserrat-Bold.ttf") format("truetype"),
		url("../assets/fonts/Montserrat/Montserrat-Bold.svg") format("svg");
	font-weight: 700;
	font-style: normal;
}

@font-face {
	font-family: "Montserrat";
	src: url("../assets/fonts/Montserrat/Montserrat-Regular.woff") format("woff"),
		url("../assets/fonts/Montserrat/Montserrat-Regular.ttf") format("truetype"),
		url("../assets/fonts/Montserrat/Montserrat-Regular.svg") format("svg");
	font-weight: normal;
	font-style: normal;
}
