$dark-shade-100: #1f2041;
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-5: rgba(31, 32, 65, 0.05);
$dark-shade-10: rgba(31, 32, 65, 0.1);
$primaryColor: #bc9cff;
$secondaryColor: #6fcf97;
$white: #ffffff;
$primaryGradient: linear-gradient(180deg, $primaryColor 0%, #8ba4f9 100%);
$primaryGradient-25: linear-gradient(180deg, rgba(188, 156, 255, 0.25), rgba(139, 164, 249, 0.25));
$primaryGradient-50: linear-gradient(180deg, rgba(188, 156, 255, 0.5), rgba(139, 164, 249, 0.5));
$secondaryGradient: linear-gradient(180deg, $secondaryColor 0%, #66d2ea 100%);    
$secondaryGradient-35: linear-gradient(180deg, rgba(111,207,151, 0.35), rgba(102,210,234, 0.35)); 
$gradient-3: linear-gradient(180deg, #FFE39C 0%, #FFBA9C 100%);
$gradient-4: linear-gradient(180deg, #919191 0%, #3D4975 100%); 
$color-3: #FFE39C;
$color-4: #919191;

$font-family: Montserrat, Arial, sans-serif;
$font-color: $dark-shade-75;
$background-color: $white;
$background-color-grey: #F4F4F6;
$font-size: 16px;
$shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
$shadow-5: 0px 10px 20px $dark-shade-5;
$card-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);